import img from '../assets/images/simodrive/1.jpg'

function CardHelp() {
    return (
        <>
            <div class="card-product border " style={{ position: 'inherit' }}>
                <div class="top-section " style={{ backgroundImage: `url(${img})`, backgroundPosition: 'center' }}>
                </div>

                <div class="bottom-section">
                    <div class="icons">
                        <div class="logo-p color-white">
                            {"brand"}
                        </div>
                    </div>

                    <span class="title" id='title' value={"model"}>{"model"}</span>

                    <div className="center">


                        <div className='col-sm-8 '>
                            <img src={"disable"} width={"120px"} alt="" />
                        </div>


                    </div>

                    <div class="row row1">
                        <div class="item">
                            <span class="big-text">${"price"}</span>
                            <span class="regular-text">Precio</span>
                        </div>
                        <div class="item">
                            <span class="big-text" style={{ fontSize: "9px" }}>{"numberPart"}</span>
                            <span class="regular-text">ID</span>
                        </div>
                        <div class="item">
                            <span class="big-text">{"conditions"}</span>
                            <span class="regular-text">Condiciones </span>
                        </div>
                    </div>
                </div>
            </div>

        </>
    );
}

export default CardHelp;